
// location section //
.section-locations {
  background-color: #fff;
  color: #000;
  position: relative;

  img {
    height: 300px;
    width: 450px;
    border-radius: 5%;
    border: 1px solid black;
    display: block;
    margin: 0 auto;
  }

  iframe {
    height: 300px;
    width: 300px;
  }

.locations-banner {
    height: auto;
    width: auto !important;
    position: relative;
    // margin:50px auto;
    background-size: cover;
    text-align: center;
  }

  .locations-header {
    text-align: center;
  }

  .locationbg1 {
    width: 1200px;
    background: $primary-color;
  }

  .box {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    padding: 1em;
    background: rgba(161, 240, 244, .3);
    display: flex;
    flex-flow: row wrap;
    flex: 0 1 100%;
    justify-content: space-evenly;
  }

  .address2 {
    margin-top: 20%;
  }

  h1{
    text-align:center;
    z-index: 99;
    margin:0 auto;
    width: 100%;
    display: block;
    position: relative;
    color: #000;
    }
  h2
  {
    text-align:center;
    font-size: 1.5rem;
    z-index: 99;
    width: 100%;
    display: block;
    position: relative;
    color: #000;
  }

  h2, h3 {
    text-align: center;
  }

  .oakland {
    position: absolute;
    margin-top: 600px;
    margin-left: 150px;
    text-align: center;
    border-bottom: 2px solid $bg;
    width: 70%;
    color: #0070cd;
    text-decoration: none;
  }
}

.slide-fade-enter {
  transform: translateY(-40px);
  opacity: 0;
}

.slide-fade-leave-to {
  transform: translateY(40px);
  opacity: 0;
}

// FAQ SECTION FAQ SECTION FAQ SECTION //

.section-faq {
  height: 100vh;
  background: #fff;
  position: relative;
  color: #000;
  text-align: center;

  .click {
    color: $primary-color;
  }
  .arrow2 {
    width: 100%;
    transform: rotate(180deg);
    display: block;
    text-align: center;
    position: absolute;
    margin: 30rem auto;
    animation: bounce 2s ease infinite;
}
    .questions {
    a {
      text-decoration: none !important;
      color: #000 !important;
      font-size: 1.5rem;
    }


  a:not([href]) {
    color: #000 !important;
    text-decoration: none;
    display: block;
  }
}

  .faq {
    padding-top: 5%;
  }

  .questions {
    padding-top: 2%;
  }

  p {
    font-size: 1.5rem;
    margin-bottom: 0 !important;
  }

  .answer {
    background-color: $primary-color;
  }

  a {
    color:#0070cd !important;
    text-decoration: none;
    font-size: 1.5rem;
  }

.whiteBox {
  height: 400px;
  width: 50%;
  background: #fff;
  transition: 2s;
  padding-right: 0 !important;
}

 .twist {
   position: relative;
    top: 10px;
    font-size: 50px;
    transition-property: all;
    transition-duration: 1s;
    transiton-timing-function: linear;
    transition-fill-mode: forwards;
    transform-origin: center;
    transform: rotate(0);
    width:27px;
    display:inline-block;
  }

.twist:hover {
    transform: rotate(360deg);
  }

  h1, h4 {
    width: 40%;
    border-bottom: 2px solid $bg;
    margin: 0 auto;
    display:block;
    }

  .media-wrapper {
    h1 {
      text-align: center;
    }
  }

  .faq-header {
    text-align: center;
    width: 30%;
    border-bottom: 3px solid $primary-color;
    margin: 0 auto;
    padding-top: 125px;
  }

  .questions {
    li
 {
    list-style-type:none;
    padding-top:50px;
    text-align: right;
    }
  }

  .answers {
      padding-left: 0 !important;
      transition: 1s;

    li {
      text-align: left;
      list-style-type: none;
      padding-top:50px;
    font-size: 35px;
    right: 0;
    // padding: 40px;
  }
}

  .qaextensive {
      text-align: center;
      width: 100%;
  }

  iframe {
    margin: 2rem auto;
    display: block;
    border: 10px solid $primary-color;
    border-radius: 1%;
  }
}

